























import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";

@Component
export default class NumberPad extends Vue {
  @Prop() readonly value!: number;
  output = this.value.toString();
  inputContent(event: MouseEvent) {
    if (this.output.length >= 14) {
      return;
    }
    if (event.target) {
      const button = event.target as HTMLButtonElement;
      const input = button.textContent as string;
      if (this.output === "0") {
        if (input === ".") {
          this.output = "0.";
        } else if ("0123456789".indexOf(input) >= 0) {
          this.output = input;
        } else {
          this.output += input;
        }
        return;
      } else if (this.output.indexOf(".") >= 0 && input === ".") {
        return;
      }
      this.output += input;
    }
  }
  remove() {
    if (this.output.length === 1) {
      this.output = "0";
    } else {
      this.output = this.output.substring(0, this.output.length - 1);
    }
  }

  count(event: MouseEvent) {
    const button = event.target as HTMLButtonElement;
    const input = button.textContent as string;
    const countType: object = {
      "+": "+",
      "-": "-"
    };
    if (this.output[this.output.length - 1] in countType) {
      return;
    } else {
      this.output = this.output + input;
    }
  }
  ok() {
    try {
      this.output = eval(this.output).toString();
    }catch (e) {
      window.alert('输入正确的金额~');
      this.output = '0';
      return
    }

    this.$emit("update:value", parseFloat(this.output));
    this.$emit("submit");
    this.output = '0'
  }
}
